import React, { useState, useEffect } from 'react'
import './properties.css'
import { Favorite, FavoriteBorder, Star, StarHalf, StarOutline, Tune, VerticalAlignBottom, VerticalAlignTop } from '@mui/icons-material'
import { useNavigate, useParams } from 'react-router-dom';
import Cookies from 'js-cookie';


function Properties() {
    const PF = process.env.REACT_APP_PUBLIC_FOLDER;
    const navigate = useNavigate();
    const currentUser = JSON.parse(Cookies.get('user'));
    const [property, setProperty] = useState([]);
    // const [likeid, setLikedid] = useState();
    const likehandle = (elem) => {
        try {
            const likeUser = async () => {
                await fetch(`/post/${elem}/like`, {
                    method: 'PUT',
                    headers: {
                        "Content-Type": "application/json"
                    }, body: JSON.stringify({
                        userId: currentUser._id
                    })
                })
            }
            likeUser();
            window.location.reload();
        } catch (err) {
            console.log(err)
        }
    }
    const params = useParams();
    const city = params.city;
    useEffect(() => {
        const fetchProperty = async () => {
            const fetchData = await fetch(`/post/property/${city}`, {
                method: 'GET',
                headers: {
                    "Content-Type": "application/json"
                }
            })
            const data = await fetchData.json();
            // console.log(data);
            setProperty(data);
        }
        fetchProperty();
    }, [city])
    // console.log(property);
    return (
        <div className='properties'>
            <div className='propertiesWrapper'>
                <div className='propertiesFilter'>
                    <div className='filterInfo'>
                        <Tune className='filterIcon' />
                        <span className='filterName'>Filter</span>
                    </div>
                    <div className='filterInfo'>
                        <VerticalAlignBottom className='filterIcon' />
                        <span className='filterName'>Highest rent first</span>
                    </div>
                    <div className='filterInfo'>
                        <VerticalAlignTop className='filterIcon' />
                        <span className='filterName'>Lowest rent first</span>
                    </div>
                </div>
                <div className='propertiesCards'>
                    {property.map(elem => (
                        <div className='propertiesCard'>
                            <div className='cardLeft'>
                                <img className='cardImg' src={PF + '/' + elem.images[0]} alt='' />
                            </div>
                            <div className='cardRight'>
                                <div className='cardRightTop'>
                                    <div className='starsCount'>
                                        {elem.propertyRating.length !== 0 && [...Array(Math.round((elem.propertyRating[0] * 1 + elem.propertyRating[1] * 1 + elem.propertyRating[2] * 1) / 3))].map((_, index) => (<Star className='starImg' />))}
                                        {elem.propertyRating.length !== 0 && [...Array((Math.round(((elem.propertyRating[0] * 1 + elem.propertyRating[1] * 1 + elem.propertyRating[2] * 1) / 3) * 2) % 2 === 1) ? 1 : 0)].map((_, index) => (<StarHalf className='starImg' />))}
                                        {elem.propertyRating.length !== 0 && [...Array(Math.round(4.5 - ((elem.propertyRating[0] * 1 + elem.propertyRating[1] * 1 + elem.propertyRating[2] * 1) / 3)))].map((_, index) => (<StarOutline className='starImg' />))}
                                    </div>
                                    <div className='interestedCountTab'>
                                        {elem.likes.includes(currentUser._id)
                                            ? <Favorite className='favouriteIcon' onClick={() => likehandle(elem._id)} />
                                            : <FavoriteBorder className='favouriteIcon' onClick={() => likehandle(elem._id)} />
                                        }
                                        <span className='interestCount'>{elem.likes.length} interested</span>
                                    </div>
                                </div>
                                <div className='cardRightCenter'>
                                    <span className='propertyName'>{elem.propertyName}</span>
                                    <span className='propertyAddress'>{elem.propertyAddress}</span>
                                    <img className='genderImg' src={PF + `/innerImg/${elem.propertyGender}.png`} alt='' />
                                </div>
                                <div className='cardRightBottom'>
                                    <div className='price'>
                                        <span className='moneyRequired'>Rs {elem.propertyRent}/-</span>
                                        <span className='timeRequired'>per month</span>
                                    </div>
                                    <div className='viewButtonTab'>
                                        <button className='viewButton' onClick={() => navigate(`/detail/${city}/${elem._id}`)}>View</button>
                                    </div>
                                </div>
                            </div>
                        </div>

                    ))}

                </div>






            </div>
        </div>
    )
}

export default Properties